import { useCallback } from 'react';
import { RoutePath, allRoutes, safeRoutes } from '../pages';
import { useAccount } from './useAccount';
import { useModalState } from './useModalState';
import { useRouteState } from './useRouteState';

export const useRouter = () => {
  const { setOpen, open } = useModalState();
  const setRoute = useRouteState((state) => state.setRoute);
  const { isConnected } = useAccount();

  const navigate = (route: RoutePath, state?: object) => {
    let validRoute = route;
    if (!allRoutes.includes(route)) {
      validRoute = isConnected ? RoutePath.WalletInfo : RoutePath.Signup;
      console.log(`Route ${route} is not a valid route, navigating to ${validRoute} instead.`);
    }

    if (isConnected) {
      if (!safeRoutes.connected.includes(route)) {
        validRoute = RoutePath.WalletInfo;
        console.log(`Route ${route} is not a valid route when connected, navigating to ${validRoute} instead.`);
      }
    } else {
      if (!safeRoutes.disconnected.includes(route)) {
        validRoute = RoutePath.Signup;
        console.log(`Route ${route} is not a valid route when disconnected, navigating to ${validRoute} instead.`);
      }
    }
    if (!open) {
      setOpen(true);
    }
    setRoute({ path: validRoute, state });
  };

  const close = useCallback(() => {
    setOpen(false);
  }, [setOpen]);

  return {
    navigate,
    close,
    isOpen: open,
  };
};
