import { detect } from 'detect-browser';
import type { RefObject } from 'react';
import { formatUnits } from 'viem/utils';
import type { WalletProps } from '../hooks/useWalletsProps';
import isMobilePhone, { phoneLocales } from './isMobilePhone';

const truncateRegex = /^(0x[a-zA-Z0-9]{4})[a-zA-Z0-9]+([a-zA-Z0-9]{4})$/;

export const defaultTokenIcon = 'https://static.particle.network/token-list/defaultToken/default.png';

interface ElementDimensions {
  width: number;
  height: number;
  topDistance: number;
  bottomDistance: number;
}

const truncateEthAddress = (address?: string, separator = '••••') => {
  if (!address) return '';
  const match = address.match(truncateRegex);
  if (!match) return address;
  return `${match[1]}${separator}${match[2]}`;
};

const detectBrowser = () => {
  const browser = detect();
  return browser?.name ?? '';
};
const detectOS = () => {
  const browser = detect();
  return browser?.os ?? '';
};

const isIOS = () => {
  const os = detectOS();
  return os.toLowerCase().includes('ios');
};
const isAndroid = () => {
  const os = detectOS();
  return os.toLowerCase().includes('android');
};
const isMobile = () => {
  return isAndroid() || isIOS();
};

export const isWalletConnectConnector = (connectorId?: string) => connectorId === 'walletConnect';

export { detectBrowser, detectOS, isAndroid, isIOS, isMobile, truncateEthAddress };

export const validPhoneNumber = (locale, countryCode: string, phone: string, t: any) => {
  const locales = phoneLocales.filter((item) => item.split('-')[1]?.toLowerCase() === locale?.toLowerCase());

  const result = locales.some((item) => {
    const res = isMobilePhone(`+${countryCode}${phone}`, item, {
      strictMode: true,
    });
    return res;
  });
  if (!result) {
    throw new Error(t?.invalidOrIncorrectNumber || '');
  } else {
    return true;
  }
};

export const hexToRgba = (hex, alpha) => {
  let r = 0,
    g = 0,
    b = 0;
  if (hex.length === 4) {
    r = parseInt(hex[1] + hex[1], 16);
    g = parseInt(hex[2] + hex[2], 16);
    b = parseInt(hex[3] + hex[3], 16);
  } else if (hex.length === 7) {
    r = parseInt(hex[1] + hex[2], 16);
    g = parseInt(hex[3] + hex[4], 16);
    b = parseInt(hex[5] + hex[6], 16);
  }
  return `rgba(${r}, ${g}, ${b}, ${alpha})`;
};

export const shortAddress = (address: string | undefined | null) => {
  if (!address) {
    return '';
  }
  if (address.length <= 10) {
    return address;
  }
  return `${address.slice(0, 6)}...${address.slice(-4)}`;
};

/**
 * Copy text to clipboard
 * @param {string} text
 * @returns {Promise<void>}
 */
export async function copyToClipboard(text: string) {
  const clipboardCopy = async () => {
    if (navigator.clipboard) {
      return navigator.clipboard.writeText(text);
    } else {
      const textarea = document.createElement('textarea');
      textarea.value = text;
      textarea.setAttribute('readonly', '');
      textarea.style.position = 'absolute';
      textarea.style.left = '-9999px';
      document.body.appendChild(textarea);

      textarea.select();
      const result = document.execCommand('copy');

      document.body.removeChild(textarea);

      if (!result) {
        throw new Error('Copy to clipboard failed');
      }
    }
  };

  return new Promise((resolve, reject) => {
    clipboardCopy().then(resolve).catch(reject);
  });
}

export const openMobileUrl = (url: string) => {
  if (url.startsWith('http')) {
    const link = document.createElement('a');
    link.href = url;
    link.target = '_blank';
    link.rel = 'noreferrer noopener';
    link.click();
  } else {
    window.location.href = url;
  }
};

export const hasPCDownloadUrl = (walletProps: WalletProps) => {
  return Boolean(
    walletProps.downloadUrls?.chrome ||
      walletProps.downloadUrls?.brave ||
      walletProps.downloadUrls?.edge ||
      walletProps.downloadUrls?.firefox ||
      walletProps.downloadUrls?.safari
  );
};

/**
 * 代币规则：
 * 最多保留小数点后9位，只展示到最后一个非零数字位。当整数位超过5位，小数后位数逐渐减少。整数为超过15位时，使用科学计数法，小数点后两位
 * @param bn
 * @param decimal
 * @param decimalPlaces
 */
export const formatTokenAmount = (value: bigint | number | `0x${string}`, decimal: number, decimalPlaces = 9) => {
  if (!value) return '0';
  if (typeof value === 'string' || typeof value === 'number') {
    value = BigInt(value);
  }
  let result = formatUnits(value, decimal);

  const [integerPart, decimalPart] = result.split('.');
  if (decimalPlaces >= 5 && integerPart === '0' && decimalPart?.startsWith(''.padEnd(decimalPlaces, '0'))) {
    return '<0.'.padEnd(decimalPlaces + 3 - 1, '0') + '1';
  }
  result = formatStringNumber(result, decimalPlaces);
  return result;
};

function formatStringNumber(num: string, n: number): string {
  let [integerPart, decimalPart] = num.split('.');

  // 如果没有小数部分，直接返回整数部分
  if (!decimalPart) {
    return integerPart as string;
  }

  // 截取小数部分的前n位
  decimalPart = decimalPart.slice(0, n);

  // 去掉小数部分末尾的零
  decimalPart = decimalPart.replace(/0+$/, '');

  // 如果小数部分全为零，返回整数部分
  if (decimalPart === '') {
    return integerPart as string;
  }

  return `${integerPart}.${decimalPart}`;
}

interface ElementDimensions {
  width: number;
  height: number;
  topDistance: number;
  bottomDistance: number;
}

export function getElementDimensionsRelativeToParent(ref: RefObject<HTMLElement>): ElementDimensions | null {
  if (!ref.current) {
    return null;
  }

  const element = ref.current;
  const parent = element.parentElement;

  if (!parent) {
    return null;
  }

  const elementRect = element.getBoundingClientRect();
  const parentRect = parent.getBoundingClientRect();

  const topDistance = elementRect.top - parentRect.top;
  const bottomDistance = parentRect.bottom - elementRect.bottom;

  const dimensions = {
    width: elementRect.width,
    height: elementRect.height,
    topDistance: topDistance,
    bottomDistance: bottomDistance,
  };

  return dimensions;
}

export function getBuyUrl(
  chainId?: number,
  address?: string,
  currency = 'USD',
  theme = 'light',
  language = 'en-US'
): string {
  const RAMP_WEB_URL = 'https://ramp.particle.network';

  const supportedNetworks: { [key: string]: string[] } = {
    '1': ['Ethereum', 'ETH'],
    '10': ['Optimism', 'ETH'],
    '56': ['Binance Smart Chain', 'BNB'],
    '101': ['Solana', 'SOL'],
    '137': ['Polygon', 'MATIC'],
    '204': ['opBNB', 'BNB'],
    '324': ['Zksync', 'ETH'],
    '5000': ['Mantle', 'MNT'],
    '8453': ['Base', 'ETH'],
    '42161': ['Arbitrum One', 'ETH'],
    '42220': ['Celo', 'CELO'],
    '43114': ['Avalanche', 'AVAX'],
    '728126428': ['Tron', 'TRX'],
    // sui TODO
  };

  if (!chainId || !address || !supportedNetworks[chainId]) {
    return '';
  }

  const chainName = supportedNetworks[chainId]?.[0] || '';

  const cryptoCoin = supportedNetworks[chainId]?.[1] || 'ETH';

  const buyParams = {
    walletAddress: address,
    fiatCoin: currency,
    cryptoCoin: cryptoCoin,
    chainName: chainName,
    showBackButton: false,
    theme: theme,
    language: language,
  };

  const queryString = new URLSearchParams(buyParams as any).toString();
  return `${RAMP_WEB_URL}/?${queryString}`;
}

export function popupWindow(url: string, title: string, w: number, h: number): Window | null {
  const left = screen.width / 2 - w / 2;
  const top = screen.height / 2 - h / 2;
  return window.open(
    url,
    title,
    'toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=' +
      w +
      ', height=' +
      h +
      ', top=' +
      top +
      ', left=' +
      left
  );
}

export const roundToEven = (value: number) => {
  return Math.floor(value / 2) * 2;
};

export function hexToRgbaColor(hex, alpha) {
  if (!hex) {
    return '';
  }
  hex = hex.replace(/^#/, '');

  if (hex.length === 3) {
    hex = hex
      .split('')
      .map((char) => char + char)
      .join('');
  }

  if (!/^[0-9A-Fa-f]{6}$/.test(hex)) {
    throw new Error('Invalid hex color format');
  }

  if (typeof alpha !== 'number' || alpha < 0 || alpha > 1) {
    throw new Error('Alpha value must be a number between 0 and 1');
  }

  const r = parseInt(hex.slice(0, 2), 16);
  const g = parseInt(hex.slice(2, 4), 16);
  const b = parseInt(hex.slice(4, 6), 16);

  return `rgba(${r}, ${g}, ${b}, ${alpha})`;
}

/**
 * Judge whether the user is registering a passkey for the first time
 * @returns {boolean} True if it's the first time, false otherwise
 */
export const isFirstRegisterPasskey = () => {
  return localStorage.getItem('passkeyRegistrationStatus') !== 'completed';
};

/**
 * Set that the user has completed passkey registration
 */
export const setFirstRegisterPasskey = () => {
  localStorage.setItem('passkeyRegistrationStatus', 'completed');
};
