export const isCoinbase = (id: string) => {
  return ['coinbaseWallet', 'coinbaseWalletSDK', 'com.coinbase.wallet'].includes(id);
};

export const isWalletConnect = (id: string) => {
  return 'walletConnect' === id;
};

export const isPasskeySmartWallet = (id: string) => {
  return 'passkeySmartWallet' === id;
};
