export const getAAIcon = (name: string): (() => JSX.Element) => {
  const icons = {
    BICONOMY: Biconomy,
    SIMPLE: Simple,
    CYBERCONNECT: CyberConnect,
    LIGHT: Light,
    XTERIO: Xterio,
    UNIVERSAL: Universal,
    COINBASE: Coinbase,
  };

  return icons[name];
};

