import { default as enUS } from './en-US.json';
import { default as jaJP } from './ja-JP.json';
import { default as koKR } from './ko-KR.json';
import { default as zhCN } from './zh-CN.json';
import { default as zhTW } from './zh-TW.json';

declare global {
  interface String {
    format(...values: (string | number)[]): string;
  }
}

export type Languages = 'en-US' | 'ja-JP' | 'zh-CN' | 'zh-TW' | 'ko-KR';

export type Translations = {
  btnContinueWithPasskey?: string;
  btnCreatePasskeyWallet?: string;
  btnLoginWithPasskey?: string;
  usePasskeyToLoginSmartAccount?: string;
};

export type TransactionType = typeof enUS;

export type TransactionKey = keyof TransactionType;

export type Locales = {
  'en-US'?: Partial<TransactionType>;
  'ja-JP'?: Partial<TransactionType>;
  'zh-CN'?: Partial<TransactionType>;
  'zh-TW'?: Partial<TransactionType>;
  'ko-KR'?: Partial<TransactionType>;
};

export const getLocale = (lang: Languages): TransactionType => {
  switch (lang) {
    case 'ja-JP':
      return jaJP;
    case 'zh-CN':
      return zhCN;
    case 'zh-TW':
      return zhTW;
    case 'ko-KR':
      return koKR;
    default:
      return enUS;
  }
};

String.prototype.format = function (...values: (string | number)[]) {
  const formatValue = this.replace(/\{\d+\}/g, (match) => {
    const index = parseInt(match.slice(1, -1), 10);
    return values[index]?.toString() ?? match; // Use the provided value or keep the placeholder if not provided
  });
  return formatValue;
};

export function formatLocale(txt: string, ...values: (string | number | React.ReactNode)[]) {
  const nodePlacer = '<></>';
  const nodes: React.ReactNode[] = [];
  const formatValue = txt.replace(/\{\d+\}/g, (match) => {
    const index = parseInt(match.slice(1, -1), 10);
    const value = values[index];
    if (typeof value === 'string' || typeof value === 'number' || typeof value === 'boolean' || !value) {
      return values[index]?.toString() ?? match; // Use the provided value or keep the placeholder if not provided
    }
    nodes.push(value);
    return nodePlacer;
  });
  if (nodes.length > 0) {
    const strs = formatValue.split(nodePlacer);
    const elements: React.ReactNode[] = [];
    for (let i = 0; i < strs.length; i++) {
      elements.push(strs[i]);
      if (nodes[i]) {
        elements.push(nodes[i]);
      }
    }
    return elements.map((item) => item);
  }
  return formatValue;
}
