import type { FC } from 'react';
import type { TransactionKey } from '../locales';
import Captcha from './captcha';
import Connecting from './connecting';
import Connectors from './connectors';
import DownloadApp from './downloadApp';
import LoginPasskey from './loginPasskey';
import PasskeySetup from './passkeySetup';
import Receive from './receive';
import SelectChain from './selectChain';
import Signup from './signup';
import WalletInfo from './walletInfo';
import WalletInfoTitle from './walletInfo/title';
import Walletconnect from './walletconnect';

export enum RoutePath {
  Signup = 'signup',
  Connectors = 'connectors',
  Captcha = 'captcha',
  Connecting = 'connecting',
  DownloadApp = 'download-app',
  SelectChain = 'select-chain',
  Walletconnect = 'walletconnect',
  WalletInfo = 'wallet-info',
  Receive = 'receive',
  PasskeySetup = 'passkey-setup',
  LoginPasskey = 'login-passkey',
}

export const safeRoutes = {
  connected: [RoutePath.WalletInfo, RoutePath.Receive],
  disconnected: [
    RoutePath.Signup,
    RoutePath.Connectors,
    RoutePath.Captcha,
    RoutePath.Connecting,
    RoutePath.DownloadApp,
    RoutePath.SelectChain,
    RoutePath.Walletconnect,
    RoutePath.PasskeySetup,
    RoutePath.LoginPasskey,
  ],
};

export const allRoutes = [...safeRoutes.connected, ...safeRoutes.disconnected] as const;

export interface IPage {
  title?: TransactionKey | FC;
  path: RoutePath;
  element: FC<any>;
}

export const pages: IPage[] = [
  {
    title: 'signupTitle',
    path: RoutePath.Signup,
    element: Signup,
  },
  {
    title: 'connectorsTitle',
    path: RoutePath.Connectors,
    element: Connectors,
  },
  {
    title: 'captchaTitle',
    path: RoutePath.Captcha,
    element: Captcha,
  },
  {
    path: RoutePath.Connecting,
    element: Connecting,
  },
  {
    path: RoutePath.DownloadApp,
    element: DownloadApp,
  },
  {
    title: 'scanWithPhone',
    path: RoutePath.Walletconnect,
    element: Walletconnect,
  },
  {
    title: 'selectChainTitle',
    path: RoutePath.SelectChain,
    element: SelectChain,
  },
  {
    title: WalletInfoTitle,
    path: RoutePath.WalletInfo,
    element: WalletInfo,
  },
  {
    title: 'receive',
    path: RoutePath.Receive,
    element: Receive,
  },
  {
    title: 'setupPasskey',
    path: RoutePath.PasskeySetup,
    element: PasskeySetup,
  },
  {
    title: 'usePasskeyToLogin',
    path: RoutePath.LoginPasskey,
    element: LoginPasskey,
  },
] as const;

export const getPage = (path: RoutePath): IPage => {
  const page = pages.find((item) => item.path === path);
  if (!page) {
    throw new Error('route path undefined');
  }

  return page;
};
